<template>
  <div class="findpsw">
    <h2 class="title">找回密码</h2>
    <img class="logo" src="../../assets/logo.png">

    <input class="form-control" type="text" placeholder="输入你的学号">
    <input class="form-control" type="password" placeholder="输入密码">
    <input class="form-control" type="password" placeholder="再次输入密码">
    <input class="form-control" type="email" placeholder="输入邮箱地址">

    <div class="rand">
      <input class="form-control" type="email" placeholder="输入验证码">
      <div class="getrand" :class="{ wait: second>0 }" @click="getrand">{{SendRand}}</div>
    </div>

    <button class="Sign-in">找回密码</button>

    <router-link class="back" :to="{name : 'login'}">
      返回登录
    </router-link>
  </div>
</template>

<script>
  var time
  export default{
    data(){
      return{
        second : 0
      }
    },
    methods:{
      getrand(){
        if(this.second === 0){
          this.second = 5;
          time = setInterval(()=>{//setInterval可以重复计时,直到clearInterval
          	this.second--;
          	if(this.second === 0){
          		clearInterval(time)
          	}
          },1000)
        }
      }
    },
    computed:{
      SendRand(){//判断获取验证码框的值，返回不同文本
      	if(this.second === 0)
      		return '获取验证码'
      	else
      		if(this.second < 10)
      			return '重新获取0' + this.second
      		else
      			return '重新获取' + this.second
      }
    }
  }
</script>

<style scoped>
  .findpsw{
    width: 90%;
    max-width: 700px;
    margin: 10px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .findpsw .title{
    text-align: center;
  }
  .findpsw .logo{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin: 10px 0;
  }
  .findpsw input{
    font-size: 18px;
    height: 40px;
    margin: 5px 0;
  }
  .findpsw .Sign-in{
    color: #f4f4f4;
    background:linear-gradient(-90deg,rgba(63,205,235,1),rgba(188,226,158,1));
    width: 100%;
    height: 40px;
    margin-top: 5px;
    border: none;
    outline: none;
    border-radius: 10px;
    cursor: pointer;
  }
  .findpsw .Sign-in:active{
    background: linear-gradient(-90deg,rgba(188,226,158,1),rgba(63,205,235,1));
    box-shadow: 1px 1px 1px #bebebe;
  }

  .findpsw .back{
    font-size: 13px;
    color: #fdd930;
    background-color: rgba(45,161,109,0.8);
    padding: 5px;
    position: absolute;
    left: 0;
    top: 35px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
  }

  .findpsw .rand{
    width: 100%;
    display: flex;
    align-items: center;
  }
  .findpsw .rand input{
    width: 70%;
  }
  .findpsw .rand .getrand{
  	/*获取验证码按键*/
    flex: 1;
  	color: #FF7D13;
  	font-size: 15px;
    padding: 9px 0;
  	border:1px solid rgba(255,168,0,0.8);
  	border-radius: 5px;
    margin-left: 5px;
  	text-align: center;
  	z-index: 2;
  }
  .findpsw .rand .wait{
  	/*等待重新获取*/
  	color: #999999 !important;
  	border:1px solid #999999;
  }
</style>



